@import url(https://fonts.googleapis.com/css2?family=Nerko+One&family=Open+Sans+Condensed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&family=Quicksand&display=swap);


.App {
  font-family: 'Open Sans Condensed', sans-serif;  
  /* font-family: 'Amatic SC'; */
  text-align: center;
  width: 98%;
  margin: 0 auto;
  border: 1px solid black;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.Home .lander {
    padding: 80px 0;
    text-align: center;
  }
  
  .Home .lander h1 {
    font-weight: 600;
  }
  .Home-image {
    height: 40vmin;
    pointer-events: none;
  }
  
.NotFound {
    padding-top: 100px;
  }
@media all and (min-width: 480px) {
    .Login {
      padding: 60px 0;
    }
  
    .Login form {
      margin: 0 auto;
      max-width: 320px;
    }
  }
  
